#intro{
    height: calc(100vh - 5rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}

.introContent{
    height: 100vh;
    width: 100vw;
    padding:2rem;
    font-size: 2.25rem;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello{
    font-size:2rem;
    font-weight:100;
}

.introName{
    color:rgb(60,120,216);
}

.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width:840px){
    .introContent{
        font-size: 5vw;
    }
}